export function favoriteReducer(state = {}, action) {
	switch (action.type) {
		case 'ADD':
			state = {
				...state,
				[action.photo.id]: action.photo
			};
			console.log('----------- NEW item is Added in store----------');

			return state;

		case 'REMOVE':
			delete state[action.photo.id];
			console.log('----------- 1 item is REMOVED in store----------');
			return state;

		case 'GET':
			return state;

		default:
			return state;
	}
}
